.banner-top {
    background: url('../img/banner-top-bg.jpg') no-repeat center;
    background-size: cover;
    text-shadow: 0 0 12px rgba(60, 61, 59, 0.25);
    color: #ffffff;

    &__title {
        margin-right: 38px;
        font-size: 28px;
        font-weight: 700;
        line-height: 32px;
        text-transform: uppercase;
        letter-spacing: 0.7px; }
    &__wrap {
        display: flex;
        align-items: center;
        min-height: 114px;
        padding: 10px 0;
        justify-content: flex-end; }
    &__btn {
        box-shadow: 0 9px 54px rgba(121, 202, 42, 0.25); } }

.header-top {
    padding: 22px 0 20px;
    background-color: #1e1e1e;
    color: #fff;
    &__btn {
        margin: 4px 19px; } }

.b-icon {
    position: relative;
    padding-left: 20px;
    font-size: 16px;
    font-weight: 300;
    line-height: 24px;
    letter-spacing: 0.4px;
    &::before {
        content: '';
        width: 15px;
        height: 15px;
        position: absolute;
        top: 4px;
        left: 0; }
    &--address::before {
        background: url('../img/sprite.svg#pin') no-repeat center;
        background-size: contain; }
    &--phone {
        padding-left: 25px;
        &::before {
            background: url('../img/sprite.svg#phone') no-repeat center;
            background-size: contain; } } }

.phone-link {
    text-decoration: none;
    font-weight: 600;
    &:hover {
        text-decoration: underline; } }

.social {
    display: flex;
    align-items: center;
    margin-left: auto;
    margin-top: 14px;
    &__item {
        display: block;
        margin-left: 15px; }
    &__link {
        @include center;
        width: 30px;
        height: 30px;
        background-color: #ffffff;
        border-radius: 50%; }
    &__icon {
        width: 60%;
        height: 60%; } }

.address-text {
    max-width: 200px; }

.header-menu {
    position: absolute;
    padding: 56px 0 10px;
    width: 100%;
    z-index: 100; }

.logo {
    display: block;
    text-decoration: none;
    &__text {
        font-size: 13px;
        font-weight: 300;
        line-height: 15px;
        letter-spacing: 0.33px;
        color: #fff; } }

.menu {
    display: flex;
    align-items: center;
    justify-content: space-between;
    font-size: 18px;
    font-weight: 600;
    line-height: 15px;
    text-transform: uppercase;
    letter-spacing: 0.45px;
    color: #fff;
    &__item {
        &--sep {
            width: 5px;
            height: 5px;
            background-color: #fff;
            border-radius: 50%;
            opacity: 0.2; } }
    &__link {
        text-decoration: none;
        &:hover {
            text-decoration: underline; } } }

.menu-btn {
    display: none;
    background-color: $mainColor;
    width: 40px;
    height: 30px;
    margin-left: auto;
    border-radius: 10px;
    align-items: center;
    justify-content: center;
    padding: 0;
    outline: none;
    border: none;
    &__icon {
        display: block;
        width: 30px;
        height: 3px;
        background-color: #fff;
        border-radius: 2px;
        position: relative;
        &::before, &::after {
            content: '';
            width: 30px;
            height: 3px;
            background-color: #fff;
            position: absolute;
            left: 0;
            border-radius: 2px;
            transition: 0.2s ease; }
        &::before {
            top: -6px; }
        &::after {
            bottom: -6px; } }
    &.active {
        .menu-btn {
            &__icon {
                background-color: $mainColor;
                &::before {
                    top: 0;
                    transform: rotate(45deg); }
                &::after {
                    bottom: 0;
                    transform: rotate(-45deg); } } } } }

.intro {
    padding: 224px 0 122px;
    background: url('../img/intro-bg.jpg') no-repeat center;
    background-size: cover;
    color: #fff;
    &-wrap {
        max-width: 770px; }
    &__title {
        position: relative;
        margin-bottom: 35px;
        font-size: 50px;
        font-weight: 700;
        line-height: 64px;
        text-transform: uppercase;
        letter-spacing: 1.25px;
        span {
            color: $mainColor; } }
    &__subtitle {
        font-size: 30px;
        font-weight: 400;
        line-height: 37px;
        letter-spacing: 0.75px; }
    &__btn {
        display: inline-block;
        position: relative;
        margin-top: 40px;
        padding: 23px 80px 23px 40px;
        line-height: 21px;
        border-radius: 30px;
        font-size: 18px;
        font-weight: 900;
        line-height: 20px;
        text-transform: uppercase;
        // Text style for "купить"
        letter-spacing: 1.53px;
        background-color: $mainColor;
        text-decoration: none;
        overflow: hidden;
        &:hover {
            .intro__btn-icon::before {
                transform: scale(10); } } }
    &__btn-text {
        position: relative;
        z-index: 10; }
    &__btn-icon {
        position: absolute;
        width: 61px;
        height: 61px;
        top: 3px;
        right: 3px;
        @include center;
        &::before {
            content: '';
            width: 100%;
            height: 100%;
            position: absolute;
            top: 0;
            left: 0;
            border-radius: 50%;
            background-color: $activeColor;
            transition: 0.3s ease; } }
    &__btn-icon-svg {
        position: relative;
        width: 15px;
        height: 15px;
        fill: #fff; }
    &__img-wrap {
        @include center;
        position: absolute;
        top: 17px;
        right: 17px;
        width: 428px;
        height: 428px;
        background-color: rgba(#fff, 0.5);
        border-radius: 50%;
        z-index: 0; } }

.advantages {
    padding: 96px 0; }

.advantages-item {
    margin: 0 auto;
    max-width: 190px;
    font-size: 18px;
    font-weight: 400;
    line-height: 26px;
    letter-spacing: 0.45px;
    text-align: center;
    &__icon-wrap {
        @include center;
        width: 143px;
        height: 143px;
        margin: 0 auto 30px;
        background-color: rgba($mainColor, 0.05);
        border-radius: 50%; }
    &__icon {
        width: 55px;
        height: 55px;
        fill: $mainColor; } }
