@media (max-width: 1200px) {
  .b-icon {
    margin-bottom: 5px; }
  .address-text {
    max-width: 100%; }

  .menu {
    font-size: 14px; }

  .intro__img-wrap {
    display: none; } }
// max-width: 1200px

@media (max-width: 991px) {
  .banner-top {
    &__wrap {
      min-height: 90px; }
    &__title {
      font-size: 20px;
      line-height: 24px; } }

  .btn {
    padding: 12px 25px;
    font-size: 16px;
    line-height: 20px; }

  .b-icon {
    font-size: 14px;
    line-height: 20px; }

  .header-top {
    &__btn {
      margin: 10px 0 0;
      padding: 8px 14px;
      font-size: 14px; } }

  .menu {
    display: none;
    position: absolute;
    padding: 15px;
    width: calc(100% - 30px);
    left: 15px;
    top: 120%;
    background-color: $mainColor;
    border-radius: 10px;
    &__item {
      margin-bottom: 10px;
      &:last-child {
        margin-bottom: 0; }
      &--sep {
        display: none; } } }

  .menu-btn {
    display: flex; }

  .header-menu {
    padding: 35px 0 10px; }

  .intro {
    padding: 150px 0 100px;
    &__title {
      font-size: 38px;
      line-height: 46px; } }

  .advantages-item {
    margin-bottom: 30px;
    &__icon-wrap {
      margin-bottom: 15px; } } }
// max-width: 991px

@media (max-width: 768px) {
  .b-icon {
    font-size: 12px;
    line-height: 16px; }

  .header-top__btn {
    padding: 5px 14px;
    font-size: 12px; }

  .social {
    margin-top: 10px;
    &__item {
      margin-left: 8px; }
    &__link {
      width: 25px;
      height: 25px; } }

  .intro {
    &__title {
      font-size: 28px;
      line-height: 36px; }
    &__subtitle {
      font-size: 20px;
      line-height: 24px; }
    &__btn {
      padding: 14px 60px 14px 20px;
      font-size: 14px; }
    &__btn-icon {
      width: 42px;
      height: 42px; } } }

// max-width: 768px

@media (max-width: 575px) {
  .banner-top {
    &__wrap {
      min-height: 70px;
      justify-content: space-between; }
    &__title {
      margin-right: 10px;
      font-size: 14px;
      line-height: 18px; } }

  .btn {
    padding: 10px 20px;
    font-size: 14px;
    line-height: 20px; }

  .header-top__btn {
    padding: 5px 10px;
    font-size: 12px;
    line-height: 14px; }

  .b-icon {
    &::before {
      width: 12px;
      height: 12px; }
    &--phone {
      padding-left: 20px;
      margin-left: auto; } }

  .intro {
    &__title {
      font-size: 18px;
      line-height: 24px; }
    &__subtitle {
      font-size: 16px;
      line-height: 20px; } }

  .advantages {
    padding: 40px 0; }

  .advantages-item {
    font-size: 14px;
    line-height: 16px;
    &__icon-wrap {
      width: 100px;
      height: 100px; }
    &__icon {
      width: 40px;
      height: 40px; } } }
// max-width: 575px

